import React, {FC} from "react"
import "../../FAPS/Legend/Legend.scss"
import {ELegendHetmap} from "./HeatmapLegend/ELegendHetmap";

interface ELegendProps {
    setShow: () => void
}

export const ELegend:
    FC<ELegendProps> = ({setShow}) => {

    const handleButtonCloseClick = () => {
        setShow()
    }

    return (
        <div className="legend shadow">

            <div className="legend__wrapper">
                <div className={"center"}><h5 className="legend__title"> </h5></div>
                <div className={'row'}>

                    <div className={'col s6'}>
                        <div className={'mb-3'}><b> (. )</b></div>
                        <ELegendHetmap/>
                    </div>
                    <div className={'col s6'}>
                        <div className={' mb-3'}><b></b></div>
                        <div className="legend__block">

                            <div
                                className="legend__placemark legend__placemark--cluster"
                                style={{backgroundImage: "url(data:image/png;base64,)"}}
                            />

                            <div className="legend__label">
                                  
                            </div>

                        </div>

                        <div className="legend__block">

                            <div
                                className="legend__placemark"
                                style={{backgroundImage: "url(data:image/png;base64,)"}}
                            >
                                <div
                                    className="legend__placemark-icon"
                                    style={{backgroundImage: "url(data:image/svg+xml;base64,)"}}
                                />
                            </div>

                            <div className="legend__label">
                                . 
                            </div>

                        </div>

                        <div className="legend__block">

                            <div
                                className="legend__placemark"
                                style={{backgroundImage: "url(data:image/png;base64,)"}}
                            />

                            <div className="legend__label">
                                -
                            </div>

                        </div>

                        <div className="legend__block">

                            <div
                                className="legend__placemark"
                                style={{backgroundImage: "url(data:image/png;base64,)"}}/>

                            <div className="legend__label">
                                -
                            </div>

                        </div>

                        <div className="legend__block">

                            <div
                                className="legend__placemark"
                                style={{backgroundImage: "url(data:image/png;base64,)"}}
                            />

                            <div className="legend__label">
                                 
                            </div>

                        </div>

                    </div>

                </div>

            </div>

            <div className="legend__controls">

                <button
                    className="legend__button legend__button--close"
                    onClick={handleButtonCloseClick}
                >
                    &#10006;
                </button>

            </div>
        </div>
    )
}
