import React, {useCallback, useContext, useEffect, useState} from 'react'
import {useHttp} from "../../hooks/http.hook"
import {useHistory, useParams} from "react-router-dom"
import './DetailPage.scss'
import getAddress from "../../functions/getAddress";
import {InlineGallery} from "../../components/FAPS/Gallery/InlineGallery"
import {Map, Placemark, YMaps} from "react-yandex-maps"
import {Scrollbars} from 'react-custom-scrollbars'
import {Button, Icon, Modal, Preloader} from "react-materialize"
import {Box} from "../../components/FAPS/Skeleton"
import {InlineRates} from "../../components/FAPS/Rates/InlineRates";
import {AuthContext} from "../../context/AuthContext";
import {roleHierarchy, Roles} from "../../roles";

export const DetailPage = () => {
    const {role} = useContext(AuthContext)

    const {loading, error, request, clearError} = useHttp()

    const history = useHistory()

    const [data, setData] = useState({})
    const [deletedData, setDeletedData] = useState(false)

    let {id} = useParams()

    useEffect(() => {
        console.log(error)
        clearError()
    }, [clearError, error])

    const fetchData = useCallback(async () => {
        try {
            const fetched = await request(`/api/detail/${id}`, 'GET', null)
            if (fetched.length === 0) {
                history.push('/error')
            }

            setData(fetched[0])
        } catch (e) {
        }
        // eslint-disable-next-line
    }, [request])

    useEffect(() => {
        fetchData()
        // eslint-disable-next-line
    }, [fetchData])

    const getPointData = () => {
        return {
            // eslint-disable-next-line
            clusterCaption: "placemark <strong>" + "</strong>"
        };
    };

    const getPointOptions = (el) => {
        return {
            preset: 'islands#violetIcon',
            iconColor: el.active === true ? '#e20101' : '#26a69a'
        };
    };

    const fetchDelete = useCallback(async (body) => {
        try {
            await request(`/api/detail/delete`, 'POST', body)

            setDeletedData(true)

            setTimeout(() => {
                history.push('/view')
            }, 5000)
        } catch (e) {
        }
        // eslint-disable-next-line
    }, [request])

    const handleDeleteModalButton = (id) => {
        fetchDelete({id})
    }

    const handleBackButtonClick = () => {
        if (history.length > 1) {
            history.goBack()
        } else {
            history.push('/')
        }
    }

    return (
        <div className="detail">

            <Scrollbars>

                <div className="detail__wrapper">

                    <div className="container">

                        <div className="detail__controls">

                            <div className="detail__back">
                                <button
                                    className="detail__back-button"
                                    onClick={handleBackButtonClick}
                                >
                                    <span> <Icon
                                        className={"material-icons"}>arrow_forward</Icon></span> {history.length > 1 ? '' : ''}
                                </button>
                            </div>
                            {roleHierarchy[role]?.includes(Roles.EXPERT) &&
                                <div className="details_buttons">
                                    <Button
                                        className="detail__button"
                                        node="button"
                                        waves="light"
                                        disabled={loading}
                                        onClick={() => history.push(`/edit/${data.id}`)}
                                    >
                                        
                                    </Button>

                                    <Button
                                        className="modal-trigger red darken-3 detail__button"
                                        href="#modal-delete"
                                        node="button"
                                        waves="light"
                                        disabled={loading}
                                    >
                                        
                                    </Button>

                                </div>
                            }
                        </div>


                        {loading ?
                            <div>
                                <Box width={15}/>
                            </div> :
                            <>
                                <div>
                                     :
                                </div>
                            </>
                        }

                        <h1 className="detail__title">
                            {loading ?
                                <div className="detail__title--replace">
                                    <Box width={100}/>
                                    <Box width={87}/>
                                </div> :
                                data.name || '.'
                            }
                        </h1>

                        <div className="detail__block">

                            {loading ?
                                <div>
                                    <Box width={50}/>
                                </div> :
                                <>
                                    <div className="detail__elem">
                                        :
                                    </div>

                                    {getAddress(data) || ''}
                                </>
                            }

                        </div>

                        <div className="detail__map">

                            {loading ?
                                <div className="detail__preloader">
                                    <Preloader
                                        active
                                        color="blue"
                                        flashing={false}
                                        size="small"
                                    />
                                </div> :

                                <YMaps>
                                    <Map
                                        state={{
                                            zoom: 12,
                                            'center': [data.latitude, data.longitude]
                                        }}
                                        className="detail__y-map"
                                    >

                                        <Placemark
                                            geometry={[data.latitude, data.longitude]}
                                            properties={getPointData(data)}
                                            options={getPointOptions(data)}
                                            modules={['geoObject.addon.balloon', 'geoObject.addon.hint']}
                                        />

                                    </Map>

                                </YMaps>

                            }

                        </div>

                        <div className="detail__info">

                            <div className="detail__block">

                                {loading ?
                                    <div>
                                        <Box width={30}/>
                                    </div> :
                                    <>
                                        <div className="detail__elem">
                                            :
                                        </div>

                                        {data.parent || ''}
                                    </>
                                }

                            </div>

                            <div className="detail__block">

                                {loading ?
                                    <div>
                                        <Box width={32}/>
                                    </div> :
                                    <>
                                        <div className="detail__elem">
                                            :
                                        </div>

                                        {data.type_name || ''}
                                    </>
                                }

                            </div>

                            <div className="detail__block">

                                {loading ?
                                    <div>
                                        <Box width={23}/>
                                    </div> :
                                    <>
                                        <div className="detail__elem">
                                            :
                                        </div>

                                        {data.phone || ''}
                                    </>
                                }

                            </div>

                            <div className="detail__block">

                                {loading ?
                                    <div>
                                        <Box width={20}/>
                                    </div> :
                                    <>
                                        <div className="detail__elem">
                                            :
                                        </div>

                                        {data.pharmacy === null ? '-' : parseInt(data.pharmacy) === 1 ? '' : ''}
                                    </>
                                }

                            </div>

                            <div className="detail__block">

                                {loading ?
                                    <div>
                                        <Box width={25}/>
                                    </div> :
                                    <>
                                        <div className="detail__elem">
                                             :
                                        </div>

                                        {data.access_to_primary_health_care === null ? '-' : parseInt(data.access_to_primary_health_care) === 1 ? '' : ''}
                                    </>
                                }

                            </div>

                            <div className="detail__block">

                                {loading ?
                                    <div>
                                        <Box width={18}/>
                                    </div> :
                                    <>
                                        <div className="detail__elem">
                                             :
                                        </div>

                                        {data.availability_of_emergency_mediical_care === null ? '-' : parseInt(data.availability_of_emergency_mediical_care) === 1 ? '' : ''}
                                    </>
                                }

                            </div>

                            <div className="detail__block">

                                {loading ?
                                    <div>
                                        <Box width={31}/>
                                    </div> :
                                    <>
                                        <div className="detail__elem">
                                             :
                                        </div>

                                        {data.staffing ? Math.round(data.staffing * 100) + '%' : 0}
                                    </>
                                }

                            </div>

                            <div className="detail__block">

                                {loading ?
                                    <div>
                                        <Box width={35}/>
                                    </div> :
                                    <>
                                        <div className="detail__elem">
                                             :
                                        </div>

                                        {data.founding_year || ''}
                                    </>
                                }

                            </div>

                            <div className="detail__block">

                                {loading ?
                                    <div>
                                        <Box width={28}/>
                                    </div> :
                                    <>
                                        <div className="detail__elem">
                                            :
                                        </div>

                                        {!isNaN(parseInt(data.population_adult)) ? parseInt(data.population_adult) : ''}
                                    </>
                                }

                            </div>

                            <div className="detail__block">

                                {loading ?
                                    <div>
                                        <Box width={24}/>
                                    </div> :
                                    <>
                                        <div className="detail__elem">
                                               :
                                        </div>

                                        {!isNaN(parseInt(data.population_child)) ? parseInt(data.population_child) : ''}
                                    </>
                                }

                            </div>

                        </div>

                    </div>

                    <div className="container">

                        <InlineGallery
                            className="detail__gallery"
                            loading={loading}
                            id={id}
                        />

                    </div>

                    <div className="container">

                        <InlineRates
                            className="detail__rates"
                            loading={loading}
                            id={id}
                        />

                    </div>

                </div>

            </Scrollbars>

            <Modal
                actions={
                    deletedData ?
                        [
                            <Button
                                node="button"
                                waves="green"
                                onClick={async () => {
                                    history.push('/view')
                                }}
                            >
                                
                            </Button>
                        ] :
                        [
                            <Button
                                className="modal-trigger red darken-3"
                                node="button"
                                waves="light"
                                style={{
                                    marginRight: '5px'
                                }}
                                disabled={loading}
                                onClick={() => handleDeleteModalButton(data.id)}
                            >
                                
                            </Button>,
                            <Button
                                modal="close"
                                node="button"
                                waves="green"
                                disabled={loading}
                            >
                                
                            </Button>
                        ]
                }
                bottomSheet={false}
                fixedFooter={false}
                header={deletedData ? ".   " : "     . ?"}
                id="modal-delete"
                open={false}
                options={{
                    dismissible: true,
                    endingTop: '30%',
                    opacity: 0.5,
                    outDuration: 250,
                    preventScrolling: true,
                    startingTop: '20%'
                }}
            >
                <div>
                    {deletedData ?
                        <div>
                            .  <strong>{data.name}</strong> !<br/>  5    
                              ""
                        </div> :
                        <div>
                               . : <br/> <strong>{data.name}</strong>
                        </div>
                    }
                </div>
            </Modal>

        </div>
    )
}
